    <h1>单品订单</h1>
  </div>
  <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
    <el-breadcrumb-item>单品订单</el-breadcrumb-item>
  </el-breadcrumb>
  <el-tree :data="buys" accordion @node-click="handleNodeClick">
  </el-tree>
</div>
</template>

<script>
import {
  buys
} from '~/data'

export default {
  data() {
    return {
      buys: buys
    }
  },
  methods: {
    handleNodeClick(data) {
      console.log(data);
    }
  }
};
</script>

<style lang="scss" scoped>


.el-tree {
    padding:0 15px 15px 15px;
}


</style>



